<template>
  <base-layout
    :is-header-showed="!$vuetify.breakpoint.mobile"
    :loading="billing.loading && driverApp.loading"
  >
    <template #header>
      <h1 class="font-weight-bold primary-dark--text">
        {{ $route.meta.pageTitle }}
      </h1>
    </template>

    <template #content>
      <div class="d-flex flex-column flex-md-row flex-shrink-1 px-lg-6 py-lg-8 px-0 py-lg-5 my-md-0 mx-lg-n2 mx-3 mt-md-0 mt-4">
        <slot></slot>
      </div>
    </template>
  </base-layout>
</template>

<script>
import BaseLayout from '@components/BaseLayout';
import { createNamespacedHelpers } from 'vuex';

const { mapState: mapSettingsState } = createNamespacedHelpers('settings');

export default {
  name: 'SettingsPageWrapper',
  components: { BaseLayout },
  computed: {
    ...mapSettingsState({
      billing: (state) => state.billingSettings,
      driverApp: (state) => state.driverAppSettings,
    }),
  },
};
</script>
