<template>
  <settings-page-wrapper>
    <navigation
      :navigation="settingsNavigation"
    />
    <v-card
      :width="$vuetify.breakpoint.mdAndDown ? '100%' : '100%'"
      class="pa-5 mx-lg-2 mx-md-2 mx-lg-5 mx-3"
    >
      <h1 class="primary-dark--text font-weight-normal text-center text-sm-start">
        Options
      </h1>
      <router-view />
    </v-card>
  </settings-page-wrapper>
</template>

<script>
import SettingsPageWrapper from '@modules/settings/components/SettingsPageWrapper';
import Navigation from '@modules/settings/components/Navigation';
import * as constants from '@modules/settings/constants';
import { createNamespacedHelpers } from 'vuex';

const { mapActions } = createNamespacedHelpers('settings');

export default {
  name: 'Settings',
  components: {
    SettingsPageWrapper,
    Navigation,
  },
  data: () => ({
    settingsNavigation: [
      {
        title: 'Driver App',
        img: 'driver-app.svg',
        path: '/settings/driver-app',
      },
      {
        title: 'Billing',
        img: 'billing.svg',
        path: '/settings/billing',
      },
    ],
  }),
  created() {
    this.getBillingSettings();
    this.getDriverAppSettings();
  },
  methods: {
    ...mapActions({
      getBillingSettings: constants.actions.GET_BILLING_SETTINGS,
      getDriverAppSettings: constants.actions.GET_DRIVERAPP_SETTINGS,
    }),
  },
};
</script>
